ul {
    list-style: none;
    padding: 0;
}

#left-panel {
    display:inline-block;
    width: 25%;
    margin-left: 5%;
    border-radius: 3px;
    border: 1px solid #00426d;
    padding: 10px 5px;
    background: #c5e8ff;
    min-height: 500px;
    vertical-align: top;
    .searchPanel,
    .resultPanel {
        display: inline-block;
        padding: 5px;
        width: 100%;
    }
    .searchCandidates {
        select {
            font-size: 18px;
            height: 420px;
        }
    }
}

#right-panel{
    display:inline-block;
    width:65%;
    margin-left: 15px;
    border:1px solid #00426d;
    border-radius:3px;
    padding:15px 10px;
    background: #c5e8ff;
    min-height:500px;
    .details{
        legend{
            display:none;
        }

        strong{
            min-width:50px;
            display:inline-block;

        }
        .radio-group{
            input{
                margin:0 .5em;
            }
        }
    }
}

.wt50{
    width:50px;
}
.wt100{
    width:50px;
}